import { Inject, Optional, Injectable } from '@angular/core';
import { CanActivate } from '@angular/router';
import {
  AccountsTransactionsJourneyConfiguration,
  AccountsTransactionsJourneyConfigurationToken,
} from './accounts-transactions-journey-config.service';

@Injectable()
export class AccountsManageGuardService implements CanActivate {
  constructor(
    @Optional()
    @Inject(AccountsTransactionsJourneyConfigurationToken)
    private readonly config: AccountsTransactionsJourneyConfiguration,
  ) {}

  canActivate() {
    return this.config?.enableManageAccounts === undefined || !!this.config.enableManageAccounts;
  }
}
